import React, { useEffect, useState, useRef } from 'react';
import Navbar from './global-components/navbar-v2';
import ErrorPage from './section-components/error';
import Footer from './global-components/footer';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import { Pie, getElementAtEvent } from 'react-chartjs-2';
import Draggable, { DraggableCore } from "react-draggable";
import ChartDataLabels from "chartjs-plugin-datalabels";
ChartJS.register(ChartDataLabels);
ChartJS.register(ArcElement, Tooltip, Legend);




const Home_V1 = ({ children, degree, handleClick, data, labels, backgroundColor, centerText = [], edgeText = [] }) => {



  const chartRef = useRef();
  const rotation = (degree - 157.5) * -1;

  return <div className='overlayContainer'>
    <Draggable

    >
      <div className='overlay'>
        <Pie
          onContextMenu={e => handleClick(e, chartRef.current)}
          ref={chartRef}
          options={{
            rotation,
            plugins: {
              legend: {
                display: false
              },
              datalabels: {
                display: true,
                color: '#36A2EB'
              },
              tooltip: {
                enabled: false
              }
            }
          }}
          data={{
            labels,
            datasets: [{

              data,
              backgroundColor,
              hoverOffset: 4,
              datalabels: {
                labels: {

                  name: {
                    color: '#8B8000',
                    font: {
                      size: 16,
                    },

                    borderRadius: 4,
                    offset: 2,
                    padding: 10,
                    formatter: (val, ctx) => centerText[ctx.dataIndex] || '',
                    align: 'start',
                    anchor: 'center',
                    clamp: true,
                  },

                  name2: {
                    color: '#fff',
                    font: {
                      size: 16,
                    },
                    backgroundColor: 'black',
                    borderRadius: 4,
                    offset: 2,
                    padding: 10,
                    formatter: (val, ctx) => edgeText[ctx.dataIndex] || '',
                    align: 'start',
                    anchor: 'end',
                    clamp: true,
                  },
                }
              }
            }]
          }}

        />
      </div>

    </Draggable>

    {children}
  </div>

}

export default Home_V1
