import React, { useEffect, useState, useRef } from 'react';
import Navbar from './global-components/navbar-v2';
import ErrorPage from './section-components/error';
import Footer from './global-components/footer';
import { Chart as ChartJS, ArcElement, Tooltip, Legend } from 'chart.js';
import Pie from './Pie';
import { getElementAtEvent  } from 'react-chartjs-2';
import { TWENTY_FOUR_MOUNTAIN } from '../cm-lib/constants/index'
import { getBazhaiChart } from '../cm-lib/EightMansion/'

const color = {
  green: 'rgba(46, 204, 113, 0.3)',
  red: 'rgb( 255, 99, 132, 0.3)',
  gray: 'rgb(192,192,192, 0.3)'
}

const Home_V1 = ({ children, degree, handleClick, facing }) => {

  
  
  const getStar = (direction, chart) => {
    return chart ? Object.keys(chart).find(k => chart[k].toLowerCase() === direction.slice(0, -1)) : ''
  }
  
  const chart =  facing ? getBazhaiChart(facing.facing) : null
  const arcInfo = TWENTY_FOUR_MOUNTAIN.map((mountain, i) => {
    return {
      label: mountain.name,
      star: getStar(mountain.facing, chart)
    }
  })

  const labels = TWENTY_FOUR_MOUNTAIN.map(e => e.name)


  

  const data = [...Array(24).keys()].map(() => 100/24)
  const backgroundColor = chart ? labels.map(label=> {

    const mountain = TWENTY_FOUR_MOUNTAIN.find(m => m.name === label)
    const directon = mountain.facing.substr(0, mountain.facing.length - 1)
    const star = Object.keys(chart).find(k => chart[k].toLowerCase() === directon)
    if(["","","",""].includes(star)) {
      return color.green
    } else {
      return color.red
    }
  }) : [
    color.green,
    color.red,
  ]
  console.log('12312')
  console.log(arcInfo)

  return <Pie 
    degree={degree}
    handleClick={(event, dom) => {
        const _selected = getElementAtEvent(dom, event);
        const mountain = TWENTY_FOUR_MOUNTAIN.find(m => m.name === labels[_selected[0]?.index])

        handleClick(event, dom, mountain)
    }}
    backgroundColor={backgroundColor}
    data={data}
    labels={labels}
    edgeText={labels}
    centerText={arcInfo.map(e => e.star)}
  >

    { children }
    <div style={{ color: 'red'}}>

    </div>
  </Pie>

}

export default Home_V1
