import * as React from "react"
import { useState } from "react"
import { HeadFC, PageProps, graphql, useStaticQuery } from "gatsby"
import Ad from '../components/ad'
import { Trans, useTranslation } from 'gatsby-plugin-react-i18next';
import Layout from '../components/layout/layout'
import Upload from '../components/Upload'
import '../assets/css/main.scss'
import Pie from '../components/EightDirection'
import Toolbar from '../components/Toolbar'
import Area from '../components/Area'
import { getFamilyMember } from '../cm-lib/IChing'


const IndexPage: React.FC = () => {

  const { t } = useTranslation();
  const [rotation, setRotation] = useState(157.5);
  const [floorplan, setFloorplan] = useState(null);
  const [dropDownCoordinate, setDropDownCoordinate] = useState({
    x: '-999999px',
    y: '-999999px',
  })

  const [selectedArea, setSelectedArea] = useState(null);
  const [configuration, setConfiguration] = useState({
    facing: ''
  })
  const [displayOption, setDisplayOption] = useState(false)

  const handleRotation = (add) => {
    if (rotation + add === -1) {
      setRotation(359)
    }
    if (rotation + add === 360) {
      setRotation(0)
    }
    const _answer = rotation + add;
    setRotation(_answer)
  }

  const handleClick = (event, dom, data) => {
    debugger;
    if (data && event.type === 'contextmenu') {
      let vw = window.innerWidth,
        vh = window.innerHeight,
        eCXinVW = event.clientX / vw * 100, // Multiplying by 100 to get percentage 
        eCYinVH = event.clientX / vh * 100; // Multiplying by 100 to get percentage
      const coord = {
        x: `${event.pageX}px`,
        y: `${event.pageY}px`,
      }
      console.log(coord)
      setDropDownCoordinate(coord)

      setSelectedArea(data)
      setDisplayOption(true)
      event.stopPropagation();
      event.preventDefault();
    }

  }

  return (
    <Layout onFloorplan={setFloorplan}>
      {
        displayOption && <Area
          x={dropDownCoordinate.x}
          y={dropDownCoordinate.y}
        >
          <div>
            <button onClick={() => {
              setConfiguration({
                ...configuration,
                facing: selectedArea
              })
            }}>Set Facing</button>


          </div>

        </Area>
      }
      {
        floorplan && <Toolbar>
          <h4>Configuration</h4>
          <button onMouseDown={() => handleRotation(-1)}>{"<<"}</button>
          <button onMouseDown={() => handleRotation(1)}>{">>"}</button>
          <input
            id="typeinp"
            type="range"
            min="0" max="359"
            value={rotation}
            onChange={(e) => setRotation(Number(e.target.value))}
            step="1" />
          <div>
            {
              rotation
            }

          </div>
          <br />
          Right Click on the compass to set Facing
          <br />
          Facing: {configuration?.facing?.name || 'Not Set'}


        </Toolbar>
      }
      <main className="page-main" onClick={() => setDisplayOption(false)}>
        <div className="section-banner relative">


          <div className="section-banner__bg" style={{ backgroundImage: "url(assets/img/bg-banner.jpg)" }} >
            <div className="uk-container uk-text-center">
              <div className="section-banner__content">
                <div className="section-banner__form">

                  {
                    floorplan && <Pie degree={rotation}
                      handleClick={handleClick}
                      facing={configuration.facing}
                    >
                      <img src={'data:image/jpeg;base64,' + btoa(floorplan)} alt="image" />
                    </Pie>
                  }
                </div>
                <div className="section-banner__bottom">
                  <Ad />
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>
    </Layout>
  )
}

export default IndexPage

export const Head: HeadFC = () => <title>Is Your Number Lucky?</title>


export const query = graphql`
  query($language: String!) {
    locales: allLocale(filter: {language: {eq: $language}}) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
